.login {
    width: 100%;
    height: 100%;
    background-image: url("../../assets/images/img/bg.jpg");
    background-size: 100% 100%;

    .login-header {
        display: flex;
        align-items: center;
        height: 80px;
        background-color: rgba(21, 20, 13, 0.5);

        h1 {
            font-size: 30px;
            color: rgb(248, 249, 250);
        }

        img {
            margin: 0 15px 0 50px;
            width: 50px;
            height: 50px;
        }
    }

    .login-content {
        width: 400px;
        height: 300px;
        background-color: white;
        margin: 150px auto;
        padding: 20px 40px;
        position: relative;
        .error-msg{
            visibility: hidden;
            position: absolute;
            top: 0;
            left: 0;
            text-align: center;
            height: 30px;
            width: 100%;
            background: #ffffff;
            color: rgb(253, 6, 6);
            font-size: 16px;
            transform: translateY(-30px);
            transition: all .3s;
            &.show{
                visibility: visible;
                transform: translateY(0);
            }
        }

        h2 {
            font-weight: bold;
            font-size: 30px;
            text-align: center;
            margin-bottom: 30px;
        }
        .login-form{
            .login-form-button{
                width: 100%;
            }
        }

    }
}
@primary-color: rgb(17,149,122);